<template>
	<view class="activity-detail-container">
		<!-- 顶部导航栏 -->
		<view class="header"
			:style="{
				height: headerHeight + 'px',
				paddingTop: statusBarHeight + 'px'
			}">
			<view class="header-content" :style="{height: menuButtonHeight + 'px'}">
				<view class="back-area" @tap="goBack">
					<text class="iconfont icon-arrow-left"></text>
				</view>
				<text class="page-title">活动详情</text>
				<view class="header-right">
					<text class="iconfont icon-share" @tap="shareActivity"></text>
				</view>
			</view>
		</view>
		
		<!-- 活动封面 -->
		<view class="activity-cover" :style="{marginTop: headerHeight + 'px'}">
			<image :src="activity.image" mode="aspectFill" class="cover-image"></image>
			<view class="cover-mask"></view>
			<view class="activity-status" :class="getStatusClass(activity)">
				<text>{{ getStatusText(activity) }}</text>
			</view>
		</view>
		
		<!-- 活动基本信息 -->
		<view class="activity-basic-info">
			<view class="activity-tag" :class="activity.tagClass">{{ activity.tag }}</view>
			<view class="activity-title">{{ activity.title }}</view>
			
			<view class="info-item">
				<text class="iconfont icon-shijian"></text>
				<text class="info-text">{{ formatDate(activity.date) }} {{ activity.time }}</text>
			</view>
			
			<view class="info-item">
				<text class="iconfont icon-location"></text>
				<text class="info-text">{{ activity.location }}</text>
			</view>
			
			<view class="info-item">
				<text class="iconfont icon-huodong"></text>
				<text class="info-text">已有 {{ activity.participants }} 人参与</text>
			</view>
		</view>
		
		<!-- 活动详情 -->
		<view class="activity-detail-section">
			<view class="section-title">
				<text class="title-text">活动详情</text>
			</view>
			<view class="activity-description">
				<rich-text :nodes="activity.description || '暂无详细描述'"></rich-text>
			</view>
		</view>
		
		<!-- 参与人员 -->
		<view class="activity-detail-section" v-if="activity.avatars && activity.avatars.length > 0">
			<view class="section-title">
				<text class="title-text">参与人员</text>
			</view>
			<view class="participants">
				<image 
					v-for="(avatar, index) in activity.avatars" 
					:key="index" 
					:src="avatar" 
					class="participant-avatar"
				></image>
				<view class="more-participants" v-if="activity.participants > activity.avatars.length">
					<text>+{{ activity.participants - activity.avatars.length }}</text>
				</view>
			</view>
		</view>
		
		<!-- 相关活动推荐 -->
		<view class="activity-detail-section">
			<view class="section-title">
				<text class="title-text">相关活动</text>
			</view>
			<scroll-view class="related-activities" scroll-x="true" show-scrollbar="false">
				<view 
					v-for="(item, index) in relatedActivities" 
					:key="index" 
					class="related-activity-item"
					@tap="viewRelatedActivity(item)"
				>
					<image :src="item.image" mode="aspectFill" class="related-activity-image"></image>
					<view class="related-activity-info">
						<text class="related-activity-title">{{ item.title }}</text>
						<text class="related-activity-time">{{ formatDate(item.date) }}</text>
					</view>
				</view>
			</scroll-view>
		</view>
		
		<!-- 底部操作栏 -->
		<view class="footer-bar">
			<view class="action-btns">
				<button class="action-btn clickable-mp" @tap="viewOrganizer">主办方介绍</button>
				<button class="action-btn primary clickable-mp" v-if="canJoin(activity)" @tap="joinActivity">立即参与</button>
				<button class="action-btn disabled" v-else>{{ activity.status === 'ended' ? '已结束' : '已参与' }}</button>
			</view>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			id: null,
			activity: {
				id: 1,
				image: '/static/images/activity1.png',
				tag: '官方',
				tagClass: 'official',
				title: '校园文化节',
				description: '<p>一年一度的校园文化节即将开幕，届时将有各种各样文化活动和表演，欢迎大家参与！</p><p>本次活动内容包括：</p><ul><li>开幕式文艺演出</li><li>各学院特色文化展示</li><li>才艺比赛</li><li>游园会</li><li>闭幕晚会</li></ul><p>活动期间会有精美礼品发放，欢迎全校师生积极参与！</p>',
				date: new Date(),
				time: '19:00-21:30',
				location: '学校体育馆',
				avatars: ['/static/images/avatar1.png', '/static/images/avatar2.png', '/static/images/avatar3.png'],
				participants: 128,
				status: 'upcoming' // upcoming, ongoing, ended
			},
			relatedActivities: [
				{
					id: 2,
					image: '/static/images/activity2.png',
					title: '创新创业大赛',
					date: new Date(new Date().getTime() + 5 * 24 * 60 * 60 * 1000), // 5天后
					status: 'upcoming'
				},
				{
					id: 3,
					image: '/static/images/activity1.png',
					title: '校园歌手大赛',
					date: new Date(new Date().getTime() + 2 * 24 * 60 * 60 * 1000), // 2天后
					status: 'upcoming'
				},
				{
					id: 4,
					image: '/static/images/activity2.png',
					title: '篮球联赛',
					date: new Date(new Date().getTime() + 7 * 24 * 60 * 60 * 1000), // 7天后
					status: 'upcoming'
				}
			],
			statusBarHeight: 0,
			menuButtonHeight: 0,
			menuButtonLeft: 0,
			menuButtonRight: 0,
			headerHeight: 0
		};
	},
	onLoad(options) {
		const sysInfo = uni.getSystemInfoSync();
		const menuButton = uni.getMenuButtonBoundingClientRect();
		this.statusBarHeight = sysInfo.statusBarHeight;
		this.menuButtonHeight = menuButton.height;
		this.menuButtonLeft = menuButton.left;
		this.menuButtonRight = menuButton.right;
		this.headerHeight = sysInfo.statusBarHeight + menuButton.height;
		if (options.id) {
			this.id = options.id;
			this.loadActivityDetail();
		}
	},
	methods: {
		// 加载活动详情数据
		loadActivityDetail() {
			// 实际项目中这里应该是网络请求获取活动详情
			// 这里使用模拟数据
			// this.activity = ...
		},
		
		// 返回上一页
		goBack() {
			uni.navigateBack();
		},
		
		// 格式化日期
		formatDate(date) {
			const months = ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'];
			return `${months[date.getMonth()]}${date.getDate()}日`;
		},
		
		// 获取状态类名
		getStatusClass(item) {
			return item.status;
		},
		
		// 获取状态文本
		getStatusText(item) {
			const statusMap = {
				'upcoming': '即将开始',
				'ongoing': '进行中',
				'ended': '已结束'
			};
			return statusMap[item.status] || '未知状态';
		},
		
		// 是否可以参与
		canJoin(item) {
			return item.status === 'upcoming' || item.status === 'ongoing';
		},
		
		// 立即参与
		joinActivity() {
			uni.showModal({
				title: '确认参与',
				content: '确定要参与该活动吗？',
				success: (res) => {
					if (res.confirm) {
						// 实际项目中这里应该发送参与请求
						uni.showToast({
							title: '报名成功',
							icon: 'success'
						});
					}
				}
			});
		},
		
		// 分享活动
		shareActivity() {
			// 实际项目中调用分享API
			uni.showToast({
				title: '分享功能开发中',
				icon: 'none'
			});
		},
		
		// 查看主办方
		viewOrganizer() {
			uni.showToast({
				title: '主办方信息页面开发中',
				icon: 'none'
			});
		},
		
		// 查看相关活动
		viewRelatedActivity(item) {
			uni.navigateTo({
				url: '/pages/circle/activity-detail?id=' + item.id
			});
		}
	}
};
</script>

<style lang="scss">
@import '@/styles/variables.scss';
@import '@/styles/common.scss';
@import '@/styles/platform-mp.scss';

.activity-detail-container {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	background-color: #F8F9FC;
	padding-top: 0;
}

.header {
	background: #fff;
	position: fixed;
	top: 0; left: 0; right: 0;
	z-index: 100;
	border-bottom: 1rpx solid #eee;
	box-sizing: border-box;
}

.header-content {
	display: flex;
	align-items: center;
	height: 100%;
}

.back-area {
	width: 44rpx;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-left: 16rpx;
	margin-right: 8rpx;
	.iconfont {
		font-size: 32rpx;
		color: #222;
	}
}

.page-title {
	font-size: 32rpx;
	color: #222;
	font-weight: 500;
	flex: 1;
	text-align: left;
}

.header-right {
	margin-right: 16rpx;
	display: flex;
	align-items: center;
	.iconfont {
		font-size: 28rpx;
		color: #888;
	}
}

/* 内容区整体下移，避免被header遮挡 */
.activity-cover,
.activity-basic-info,
.activity-detail-section,
.participants,
.related-activities,
.footer-bar {
	margin-top: 0;
}

.activity-cover {
	position: relative;
	height: 500rpx;
	width: 100%;
	
	.cover-image {
		width: 100%;
		height: 100%;
	}
	
	.cover-mask {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: linear-gradient(to bottom, rgba(0,0,0,0.2), rgba(0,0,0,0.5));
	}
	
	.activity-status {
		position: absolute;
		right: 30rpx;
		bottom: 30rpx;
		padding: 10rpx 30rpx;
		border-radius: 30rpx;
		font-size: 24rpx;
		font-weight: bold;
		
		&.upcoming {
			background: rgba($primary-color, 0.9);
			color: #fff;
		}
		
		&.ongoing {
			background: rgba($success-color, 0.9);
			color: #fff;
		}
		
		&.ended {
			background: rgba(#999, 0.9);
			color: #fff;
		}
	}
}

.activity-basic-info {
	margin-top: -60rpx;
	margin-horizontal: 30rpx;
	padding: 30rpx;
	background: #fff;
	border-radius: 16rpx;
	box-shadow: 0 4rpx 16rpx rgba(0, 0, 0, 0.08);
	position: relative;
	z-index: 10;
	
	.activity-tag {
		display: inline-block;
		padding: 6rpx 20rpx;
		border-radius: 20rpx;
		font-size: 24rpx;
		margin-bottom: 16rpx;
		
		&.official {
			background-color: rgba($primary-color, 0.1);
			color: $primary-color;
		}
		
		&.club {
			background-color: rgba(#5B2EE6, 0.1);
			color: #5B2EE6;
		}
		
		&.academic {
			background-color: rgba(#06A788, 0.1);
			color: #06A788;
		}
		
		&.entertainment {
			background-color: rgba($warning-color, 0.1);
			color: $warning-color;
		}
		
		&.sports {
			background-color: rgba(#FF3366, 0.1);
			color: #FF3366;
		}
	}
	
	.activity-title {
		font-size: 36rpx;
		font-weight: bold;
		color: #333;
		margin-bottom: 24rpx;
		line-height: 1.3;
	}
	
	.info-item {
		display: flex;
		align-items: center;
		margin-bottom: 16rpx;
		
		&:last-child {
			margin-bottom: 0;
		}
		
		.iconfont {
			font-size: 28rpx;
			color: $primary-color;
			margin-right: 12rpx;
		}
		
		.info-text {
			font-size: 28rpx;
			color: #666;
		}
	}
}

.activity-detail-section {
	margin: 30rpx;
	padding: 30rpx;
	background: #fff;
	border-radius: 16rpx;
	box-shadow: 0 2rpx 12rpx rgba(0, 0, 0, 0.04);
	
	.section-title {
		margin-bottom: 20rpx;
		position: relative;
		
		&:after {
			content: '';
			position: absolute;
			left: 0;
			bottom: -10rpx;
			width: 60rpx;
			height: 4rpx;
			background: $primary-color;
			border-radius: 2rpx;
		}
		
		.title-text {
			font-size: 32rpx;
			font-weight: bold;
			color: #333;
		}
	}
	
	.activity-description {
		font-size: 28rpx;
		color: #666;
		line-height: 1.6;
	}
}

.participants {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	
	.participant-avatar {
		width: 80rpx;
		height: 80rpx;
		border-radius: 50%;
		margin-right: 16rpx;
		margin-bottom: 16rpx;
		border: 2rpx solid #fff;
		box-shadow: 0 2rpx 4rpx rgba(0, 0, 0, 0.1);
	}
	
	.more-participants {
		width: 80rpx;
		height: 80rpx;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		background: #f5f5f5;
		color: #666;
		font-size: 24rpx;
		margin-right: 16rpx;
		margin-bottom: 16rpx;
	}
}

.related-activities {
	white-space: nowrap;
	margin: 0 -10rpx;
}

.related-activity-item {
	display: inline-block;
	width: 300rpx;
	margin: 0 10rpx;
	border-radius: 12rpx;
	overflow: hidden;
	background: #fff;
	box-shadow: 0 2rpx 8rpx rgba(0, 0, 0, 0.05);
	
	.related-activity-image {
		width: 100%;
		height: 160rpx;
	}
	
	.related-activity-info {
		padding: 16rpx;
		
		.related-activity-title {
			font-size: 26rpx;
			color: #333;
			font-weight: 500;
			margin-bottom: 8rpx;
			white-space: normal;
			display: -webkit-box;
			-webkit-line-clamp: 2;
			-webkit-box-orient: vertical;
			overflow: hidden;
			line-height: 1.3;
		}
		
		.related-activity-time {
			font-size: 22rpx;
			color: #999;
		}
	}
}

.footer-bar {
	padding: 20rpx 30rpx;
	background: #fff;
	border-top: 1rpx solid rgba(0, 0, 0, 0.05);
	position: fixed;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 90;
	box-shadow: 0 -2rpx 10rpx rgba(0, 0, 0, 0.03);
	
	.action-btns {
		display: flex;
		justify-content: space-between;
		
		.action-btn {
			flex: 1;
			margin: 0 10rpx;
			height: 80rpx;
			line-height: 80rpx;
			border-radius: 40rpx;
			font-size: 28rpx;
			font-weight: 500;
			text-align: center;
			background: #f5f5f5;
			color: #666;
			border: none;
			
			&.primary {
				background: $primary-color;
				color: #fff;
				box-shadow: 0 4rpx 12rpx rgba($primary-color, 0.3);
			}
			
			&.disabled {
				background: #eee;
				color: #999;
			}
		}
	}
}
</style> 